@import '../../App.scss';


.poster__overlay{
    background-color: rgba(0, 0, 0, 0.5);
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;    
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    padding: 1rem;

    .poster__title{
        font-size: 1.5rem;
        font-weight: 900;
        margin-bottom: 0.5rem;
        text-align: left;        
    }

    .container{
        display: flex;
        flex-direction: row;
        width: 100%;
        gap: 2rem;
        font-size: 1rem;
        font-weight: 400;
        margin-bottom: 0.5rem;
        text-align: left;

        .poster__rating{
            font-weight: 800;
            display: flex;
            flex-direction: row;
            align-items: center;
            gap: 0.5rem;
            justify-content: center;
        }
    }

    
    .poster__overview{
        display: none;

        @media (min-width: 768px) {
            display: block;
            text-align: left;
            font-size: 0.8rem;
            font-weight: 400;
            margin-bottom: 0.5rem;
            font-style: italic;
        }
    }

}
